<template>
<div style="width:100%;height:100%" @click="demo">
<Order :baldic="baldic"></Order>
<img src="../style/img/1.jpg" style="position:absolute;buttom:30px;left:40px;" id="shoping_car">
    </div>
</template>
<script>
import Order from "./Order";

export default {
  components: {
    Order
  },
  methods:{
    flyball(e) {
      e = e || this.event;
      let slsctor ="#shoping_car";
      var aat = document.querySelector(slsctor);
      let domrect = aat.getBoundingClientRect();
      this.baldic = {
        sx: e.clientX, //出发点x坐标
        sy: e.clientY, //
        tx: domrect.x,
        ty: domrect.y
      };
    },
    demo(){
        this.flyball(event);
    }
  }
}
</script>



